.todoLi{
  margin: 10px 20px 10px 10px;
  border-right: 10px;
  list-style-type: circle;
  color: #393d3f;
  font-size: 18px;
  font-weight: 300px;
}

.liButton{
  display: inline-block;
  border:1px solid #ff8fa3;
  margin-left: 0.7%;
  padding:0px;
  width: 40px;
  height: 30px;
  border-radius: 5px;
  color: whitesmoke;
  border-color: whitesmoke;
  text-decoration: none;
  background-color: transparent;
}

.delButton{
  display: inline-block;
  border:2px solid #ff8fa3;
  margin-left: 0.6%;
  padding:0px;
  width: 30px;
  height:30px;
  border-radius: 5px;
  background-color: transparent;
  color: #ff8fa3;
}

.liButton:hover{
  background-color: #ffffff;
  color:gray;
}

.delButton:hover{
  background-color: #ff8fa3;
  color:white;
}
